/**
 * Main script for converting Pikachu Volleyball replay files to video
 * Real-time recording with MediaRecorder at 30 FPS
 */
'use strict';
import { settings } from '@pixi/settings';
import { SCALE_MODES } from '@pixi/constants';
import { Renderer, BatchRenderer, autoDetectRenderer } from '@pixi/core';
import { Prepare } from '@pixi/prepare';
import { Container } from '@pixi/display';
import { Loader } from '@pixi/loaders';
import { SpritesheetLoader } from '@pixi/spritesheet';
import { Ticker } from '@pixi/ticker';
import { CanvasRenderer } from '@pixi/canvas-renderer';
import { CanvasSpriteRenderer } from '@pixi/canvas-sprite';
import { CanvasPrepare } from '@pixi/canvas-prepare';
import '@pixi/canvas-display';
import { ASSETS_PATH } from '../offline_version_js/assets_path.js';
import { PikachuVolleyballReplay } from './pikavolley_replay.js';
import { setGetSpeechBubbleNeeded, enableChat } from '../chat_display.js';
import { serialize } from '../utils/serialize.js';
import { getHashCode } from '../utils/hash_code.js';
import '../../style.css';

/**
 * Replay to Video Converter with MediaRecorder (Real-time recording)
 */
class ReplayToVideoConverter {
  constructor() {
    // Setup PIXI
    Renderer.registerPlugin('prepare', Prepare);
    Renderer.registerPlugin('batch', BatchRenderer);
    CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
    CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
    Loader.registerPlugin(SpritesheetLoader);
    settings.RESOLUTION = 2;
    settings.SCALE_MODE = SCALE_MODES.NEAREST;
    settings.ROUND_PIXELS = true;

    this.renderer = null;
    this.stage = null;
    this.loader = null;
    this.ticker = null;
    this.pikaVolley = null;
    this.currentPack = null;
    
    this.mediaRecorder = null;
    this.recordedChunks = [];
    this.isRecording = false;
  }

  updateStatus(message) {
    const statusEl = document.getElementById('status');
    if (statusEl) {
      statusEl.textContent = message;
    }
    console.log(message);
  }

  handleRecordButtonClick() {
    if (this.isRecording) {
      this.updateStatus('이미 처리 중입니다...');
      return;
    }

    const fileInput = document.createElement('input');
    fileInput.type = 'file';
    fileInput.accept = '.txt';
    fileInput.onchange = (e) => {
        // @ts-ignore
      const file = e.target.files[0];
      if (file) {
        this.loadReplayFile(file);
      }
    };
    fileInput.click();
  }

  loadReplayFile(file) {
    this.updateStatus('리플레이 파일 로딩 중...');
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = true;

    // Show loading box
    const loadingBox = document.getElementById('loading-box');
    if (loadingBox) loadingBox.classList.remove('hidden');

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        // @ts-ignore
        const packWithComment = JSON.parse(event.target.result);
        const pack = packWithComment.pack;
        const hash = pack.hash;
        pack.hash = 0;
        if (hash !== getHashCode(serialize(pack))) {
          throw new Error('파일 내용이 해시 코드와 일치하지 않습니다');
        }
        this.initializeRenderer(pack);
      } catch (err) {
        console.error(err);
        this.updateStatus('파일 로딩 실패: ' + err.message);
        // @ts-ignore
        if (recordBtn) recordBtn.disabled = false;
        if (loadingBox) loadingBox.classList.add('hidden');
      }
    };
    reader.onerror = () => {
      this.updateStatus('파일 읽기 실패');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
      if (loadingBox) loadingBox.classList.add('hidden');
    };
    reader.readAsText(file);
  }

  initializeRenderer(pack) {
    this.updateStatus('게임 에셋 로딩 중...');
    this.currentPack = pack;

    // Setup renderer
    this.renderer = autoDetectRenderer({
      width: 432,
      height: 304,
      antialias: false,
      backgroundColor: 0x000000,
      backgroundAlpha: 1,
      forceCanvas: true,
      preserveDrawingBuffer: true,
    });

    this.stage = new Container();
    this.ticker = new Ticker();
    this.ticker.minFPS = 1;
    this.loader = new Loader();

    // Append canvas to container (hidden from view during recording)
    const container = document.querySelector('#game-canvas-container');
    container.innerHTML = '';
    container.appendChild(this.renderer.view);
    
    // Hide canvas during recording, show only progress
    // @ts-ignore
    container.style.display = 'none';

    // Setup assets
    const TEXTURES = ASSETS_PATH.TEXTURES;
    TEXTURES.WITH_COMPUTER = TEXTURES.WITH_FRIEND;

    this.loader.add(ASSETS_PATH.SPRITE_SHEET);
    for (const prop in ASSETS_PATH.SOUNDS) {
      this.loader.add(ASSETS_PATH.SOUNDS[prop]);
    }

    this.setupLoaderProgressBar();
    this.loader.load(() => this.setupGame(pack));
  }

  setupLoaderProgressBar() {
    const progressBar = document.getElementById('progress-bar');
    if (progressBar) {
      this.loader.onProgress.add(() => {
        progressBar.style.width = `${this.loader.progress}%`;
      });
    }
  }

  setupGame(pack) {
    this.updateStatus('게임 초기화 중...');

    this.pikaVolley = new PikachuVolleyballReplay(
      this.stage,
      this.loader.resources,
      pack.roomID,
      pack.nicknames,
      pack.partialPublicIPs,
      pack.inputs,
      pack.options,
      pack.chats
    );

    // @ts-ignore
    setGetSpeechBubbleNeeded(this.pikaVolley);

    // Enable chat display
    this.pikaVolley.willDisplayChat = true;
    enableChat(true);

    // Enable nickname and IP display by checking checkboxes programmatically
    const showNicknamesCheckbox = document.getElementById('show-nicknames-checkbox');
    const showIPsCheckbox = document.getElementById('show-ip-addresses-checkbox');
    const showChatCheckbox = document.getElementById('show-chat-checkbox');
    
    if (showNicknamesCheckbox) {
      // @ts-ignore
      showNicknamesCheckbox.checked = true;
      showNicknamesCheckbox.dispatchEvent(new Event('change'));
    }
    
    if (showIPsCheckbox) {
      // @ts-ignore
      showIPsCheckbox.checked = true;
      showIPsCheckbox.dispatchEvent(new Event('change'));
    }
    
    if (showChatCheckbox) {
      // @ts-ignore
      showChatCheckbox.checked = true;
      showChatCheckbox.dispatchEvent(new Event('change'));
    }

    // Disable BGM completely
    this.pikaVolley.audio.turnBGMVolume(false);
    if (this.pikaVolley.audio.sounds.bgm && this.pikaVolley.audio.sounds.bgm.center) {
      this.pikaVolley.audio.sounds.bgm.center.stop();
      // Override play method to prevent BGM
      // @ts-ignore
      this.pikaVolley.audio.sounds.bgm.center.play = function() {};
      this.pikaVolley.audio.sounds.bgm.center.isPlaying = false;
    }

    // Enable SFX (sound effects) - though they won't be captured
    this.pikaVolley.audio.turnSFXVolume(true);

    // Setup ticker for rendering
    this.ticker.add(() => {
      this.renderer.render(this.stage);
      this.pikaVolley.gameLoop();
    });

    setTimeout(() => this.startRecording(), 100);
  }

  startRecording() {
    this.updateStatus('녹화 준비 중...');
    this.recordedChunks = [];
    this.isRecording = true;

    const canvas = this.renderer.view;
    
    try {
      // Create canvas stream at 30 FPS
      const fps = 30;
      const canvasStream = canvas.captureStream(fps);

      // Setup MediaRecorder
      let mimeType = 'video/webm;codecs=vp9';
      if (!MediaRecorder.isTypeSupported(mimeType)) {
        mimeType = 'video/webm;codecs=vp8';
        if (!MediaRecorder.isTypeSupported(mimeType)) {
          mimeType = 'video/webm';
        }
      }

      this.mediaRecorder = new MediaRecorder(canvasStream, {
        mimeType: mimeType,
        videoBitsPerSecond: 2500000, // 2.5 Mbps
      });

      this.mediaRecorder.ondataavailable = (e) => {
        if (e.data.size > 0) {
          this.recordedChunks.push(e.data);
        }
      };

      this.mediaRecorder.onstop = () => {
        this.saveVideoFile();
      };

      this.mediaRecorder.onerror = (e) => {
        console.error('MediaRecorder 에러:', e);
        this.updateStatus('녹화 중 에러 발생');
        this.isRecording = false;
        const recordBtn = document.getElementById('record-btn');
        // @ts-ignore
        if (recordBtn) recordBtn.disabled = false;
      };

      // Start recording
      this.mediaRecorder.start(100); // Collect data every 100ms

      // Start game ticker at 30 FPS
      this.ticker.maxFPS = 30;
      this.ticker.start();

      this.updateStatus(`녹화 중... 0%`);
      this.monitorProgress();

    } catch (err) {
      console.error('녹화 시작 실패:', err);
      this.updateStatus('녹화 시작 실패: ' + err.message);
      this.isRecording = false;
      const recordBtn = document.getElementById('record-btn');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
    }
  }

  monitorProgress() {
    const totalFrames = this.pikaVolley.inputs.length;
    const progressBar = document.getElementById('progress-bar');
    
    const checkInterval = setInterval(() => {
      if (!this.pikaVolley || !this.isRecording) {
        clearInterval(checkInterval);
        return;
      }

      const currentFrame = this.pikaVolley.replayFrameCounter;
      const progress = Math.floor((currentFrame / totalFrames) * 100);
      
      this.updateStatus(`녹화 중... ${progress}%`);
      if (progressBar) {
        progressBar.style.width = `${progress}%`;
      }

      if (currentFrame >= totalFrames) {
        clearInterval(checkInterval);
        this.stopRecording();
      }
    }, 500);
  }

  stopRecording() {
    if (!this.isRecording) return;

    this.updateStatus('녹화 완료, 파일 저장 중...');
    this.ticker.stop();
    
    if (this.mediaRecorder && this.mediaRecorder.state !== 'inactive') {
      this.mediaRecorder.stop();
    } else {
      this.saveVideoFile();
    }
  }

  saveVideoFile() {
    if (this.recordedChunks.length === 0) {
      this.updateStatus('녹화된 데이터가 없습니다');
      this.isRecording = false;
      const recordBtn = document.getElementById('record-btn');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
      return;
    }

    const blob = new Blob(this.recordedChunks, { type: 'video/webm' });
    const url = URL.createObjectURL(blob);
    
    const d = new Date();
    const dateStr = `${d.getFullYear()}${('0' + (d.getMonth() + 1)).slice(-2)}${('0' + d.getDate()).slice(-2)}`;
    const timeStr = `${('0' + d.getHours()).slice(-2)}${('0' + d.getMinutes()).slice(-2)}`;
    
    let filename = `${dateStr}_${timeStr}_pikavolley_replay.webm`;
    
    const pack = this.currentPack;
    if (pack && pack.nicknames && pack.nicknames.length === 2) {
      const nick1 = pack.nicknames[0].replace(/[/\\?%*:|"<>]/g, '_');
      const nick2 = pack.nicknames[1].replace(/[/\\?%*:|"<>]/g, '_');
      filename = `${dateStr}_${timeStr}_${nick1}_vs_${nick2}.webm`;
    }

    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.style.display = 'none';
    document.body.appendChild(a);
    a.click();
    
    setTimeout(() => {
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    }, 100);

    this.updateStatus('완료! 다른 리플레이를 변환하려면 버튼을 다시 클릭하세요.');
    this.isRecording = false;
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = false;
    this.recordedChunks = [];
    
    // Hide loading box
    const loadingBox = document.getElementById('loading-box');
    if (loadingBox) loadingBox.classList.add('hidden');
  }
}

// Adjust assets path
function adjustAssetsPath() {
  ASSETS_PATH.SPRITE_SHEET = '../' + ASSETS_PATH.SPRITE_SHEET;
  for (const prop in ASSETS_PATH.SOUNDS) {
    ASSETS_PATH.SOUNDS[prop] = '../' + ASSETS_PATH.SOUNDS[prop];
  }
}

adjustAssetsPath();

// Initialize converter
const converter = new ReplayToVideoConverter();
const recordBtn = document.getElementById('record-btn');
if (recordBtn) {
  recordBtn.addEventListener('click', () => converter.handleRecordButtonClick());
}

// Setup UI event listeners for nickname/IP/chat display
document.addEventListener('DOMContentLoaded', () => {
  const showNicknamesCheckbox = document.getElementById('show-nicknames-checkbox');
  const showIPsCheckbox = document.getElementById('show-ip-addresses-checkbox');
  const showChatCheckbox = document.getElementById('show-chat-checkbox');
  
  const player1NicknameElem = document.getElementById('player1-nickname');
  const player2NicknameElem = document.getElementById('player2-nickname');
  const player1IPElem = document.getElementById('player1-partial-ip');
  const player2IPElem = document.getElementById('player2-partial-ip');

  if (showNicknamesCheckbox) {
    showNicknamesCheckbox.addEventListener('change', () => {
      // @ts-ignore
      if (showNicknamesCheckbox.checked) {
        if (player1NicknameElem) player1NicknameElem.classList.remove('hidden');
        if (player2NicknameElem) player2NicknameElem.classList.remove('hidden');
      } else {
        if (player1NicknameElem) player1NicknameElem.classList.add('hidden');
        if (player2NicknameElem) player2NicknameElem.classList.add('hidden');
      }
    });
  }

  if (showIPsCheckbox) {
    showIPsCheckbox.addEventListener('change', () => {
      // @ts-ignore
      if (showIPsCheckbox.checked) {
        if (player1IPElem) player1IPElem.classList.remove('hidden');
        if (player2IPElem) player2IPElem.classList.remove('hidden');
      } else {
        if (player1IPElem) player1IPElem.classList.add('hidden');
        if (player2IPElem) player2IPElem.classList.add('hidden');
      }
    });
  }

  if (showChatCheckbox) {
    showChatCheckbox.addEventListener('change', () => {
      // @ts-ignore
      if (showChatCheckbox.checked) {
        enableChat(true);
      } else {
        enableChat(false);
      }
    });
  }
});
